.task {
  display: -ms-flexbox;
  display: flex;
  background: lightblue;
  background-color: #67B8DE;
  width: 100%;
  margin: 0 0 10px 0;
  padding: 5px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.task--completed {
  opacity: 0.3;
}

.task--completed .task-text {
  text-decoration: line-through;
}

.task-done {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-item-align: start;
      align-self: flex-start;
  width: 24px;
  height: 24px;
  margin: 0 5px 0 0;
  border: 2px solid black;
  background-color: transparent;
  font-size: 0;
  cursor: pointer;
}

.task--completed .task-done {
  background-image: url('./done.svg');
  background-position: center center;
  background-repeat: none;
}

.task-text {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0;
  padding: 0;
}

.task-del {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-item-align: end;
      align-self: flex-end;
  width: 24px;
  height: 24px;
  border: none;
  background-color: transparent;
  font-size: 0;
  cursor: pointer;
  background-image: url('./clear.svg');
  background-position: center center;
  background-repeat: none;
}


